import React from "react";
import Prosidebar from "../../components/prosidebar";
import useDarkMode from "../../hook/useDarkMode";
import a from "../../img/test1.jpg";
import b from "../../img/test2.jpg";

export const Products = () => {
  const [colorTheme, setTheme] = useDarkMode();

  return (
    <div className=" flex bg-white dark:bg-slate-800">
      <Prosidebar className="absolute" />
      <div className="w-full h-screen overflow-auto transition-all duration-1000 ease-in-out ">
        <div className="text-left p-4 w-fit h-fit">
          <div className="flex flex-col absolute top-0 right-3 p-4">
            {colorTheme == "dark" ? (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
                className="w-6 h-6 cursor-pointer"
                onClick={() => {
                  setTheme(colorTheme);
                }}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M21.752 15.002A9.718 9.718 0 0118 15.75c-5.385 0-9.75-4.365-9.75-9.75 0-1.33.266-2.597.748-3.752A9.753 9.753 0 003 11.25C3 16.635 7.365 21 12.75 21a9.753 9.753 0 009.002-5.998z"
                />
              </svg>
            ) : (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
                className="w-6 h-6 cursor-pointer text-white"
                onClick={() => {
                  setTheme(colorTheme);
                }}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M12 3v2.25m6.364.386l-1.591 1.591M21 12h-2.25m-.386 6.364l-1.591-1.591M12 18.75V21m-4.773-4.227l-1.591 1.591M5.25 12H3m4.227-4.773L5.636 5.636M15.75 12a3.75 3.75 0 11-7.5 0 3.75 3.75 0 017.5 0z"
                />
              </svg>
            )}
          </div>
        </div>
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-5 text-white p-3 transition-all duration-1000 ease-in-out">
          <div className="self-center">
            <img src={a} />
          </div>
          <div className="self-center ">
            <img src={b} />
          </div>
          <div className="self-center">
            <img src={a} />
          </div>
          <div className="self-center">
            <img src={b} />
          </div>
          <div className="self-center">
            <img src={a} />
          </div>
          <div className="self-center">
            <img src={b} />
          </div>
          <div className="self-center">
            <img src={a} />
          </div>
          <div className="self-center">
            <img src={a} />
          </div>
          <div className="self-center">
            <img src={a} />
          </div>

          <div className="self-center">
            <img src={b} />
          </div>

          <div className="self-center">
            <img src={a} />
          </div>
        </div>
      </div>
    </div>
  )
}
